/**
 * convert date to the Datepicker format
 * @param dateString the date string to convert
 * @returns
 */
export const convertDateFormat = (dateString: string | null) => {
  if (!dateString) return;
  // Check if the date is already in DD/MM/YY format
  const datePattern = /^\d{2}\/\d{2}\/\d{2}$/;
  if (!datePattern.test(dateString)) {
    return undefined;
  }

  return dateString;
};

/**
 * Determines if the date string is in the format DD/MM/YY
 * @param dateString input date string
 * @returns boolean
 */

export const isValidDateFormat = (dateString: string | null) => {
  if (!dateString) return false;

  const datePattern = /^\d{2}\/\d{2}\/\d{2}$/;
  return datePattern.test(dateString);
};

/**
 *
 * @param dateString the date string to convert
 * @returns date string in the format YYYY-MM-DD
 */

export const convertToMewsDateFormat = (dateString: string | null) => {
  if (!dateString) return;

  // Check if the date is not in DD/MM/YY format
  let datePattern = /^\d{2}\/\d{2}\/\d{2}$/;
  if (!datePattern.test(dateString)) {
    return undefined;
  }

  // Check if the date is already in YYYY-MM-DD format
  datePattern = /^\d{4}-\d{2}-\d{2}$/;
  if (datePattern.test(dateString)) {
    return dateString;
  }

  const [day, month, year] = dateString.split('/');
  return `20${year}-${month}-${day}`;
};
