import { stagingApartmentsPageUrl } from './constants';

export const getApartmentsPageUrl = () => {
  const currentUrl = window.location.href;
  if (currentUrl.includes('webflow.io')) {
    return stagingApartmentsPageUrl;
  }
  // if (currentUrl.includes('en.thelander')) {
  //   return stagingApartmentsPageUrl; //enLocaleProductionApartmentsPageUrl;
  // }
  return stagingApartmentsPageUrl;
};
