import type { BookingParameters, SearchParameters } from '$types/api';

export const constructUrl = (searchParams: SearchParameters | BookingParameters) => {
  let url = '?';
  for (const [key, value] of Object.entries(searchParams)) {
    if (value === undefined || value === null || value === 'undefined-undefined-') continue;
    url = url + `${key}=${value}&`;
  }
  return url;
};
