import type { SearchParameters } from '$types/api';

import { CitiesDropdown } from './CitiesDropdown';
import { SELECTORS } from './constants';
import { constructUrl } from './constructUrl';
import { convertDateFormat, isValidDateFormat } from './convertDateFormat';
import { getApartmentsPageUrl } from './getApartmentsPageUrl';
import { parseUrlParams } from './parseUrlParams';
import { reverseDateFormat } from './reverseDateFormat';

export class SearchBar {
  public cityName: string;
  public cityId: string;
  public checkInDate: string | null;
  public checkOutDate: string | null;
  public totalCount: string;
  public noOfPeople: string;
  public infantCount: string;
  public babyCount: string;

  public searchForm: HTMLElement;
  public checkInDateElement: HTMLDivElement;
  public checkOutDateElement: HTMLInputElement;
  public totalCountElement: HTMLInputElement;
  public noOfpeopleElement: HTMLElement;

  public cityDropdownInstance: CitiesDropdown;

  public queryUrl: string | undefined;

  constructor(citiesDropdownInstance: CitiesDropdown) {
    this.cityDropdownInstance = citiesDropdownInstance;
    this.cityName = this.cityDropdownInstance.getCityName();
    this.cityId = this.cityDropdownInstance.getCityId();

    // // declaring and selecting all variables and inputs elements
    this.checkInDateElement = document.querySelector(SELECTORS.checkinDatePicker) as HTMLDivElement;

    this.checkOutDateElement = document.querySelector(
      SELECTORS.checkoutDatePicker
    ) as HTMLInputElement;

    this.noOfpeopleElement = document.querySelector(SELECTORS.noOfPeople) as HTMLElement;
    this.noOfPeople = this.noOfpeopleElement.textContent as string;

    this.searchForm = document.querySelector(SELECTORS.searchForm) as HTMLFormElement;
    this.searchForm.addEventListener('submit', (e) => this.handleSearch(e));
    this.queryUrl = undefined;
    this.getAndUpdateSearchData();
  }

  public isDateRangeValid() {
    if (!this.checkInDate || !this.checkOutDate) return false;

    const checkInDate = new Date(reverseDateFormat(this.checkInDate));
    const checkOutDate = new Date(reverseDateFormat(this.checkOutDate));

    return checkInDate < checkOutDate;
  }

  public getAndUpdateSearchData() {
    const parsedUrlParams = parseUrlParams();

    if (!parsedUrlParams) return;

    this.cityId = parsedUrlParams.cityId ?? '';
    this.cityName = parsedUrlParams.cityName || '';

    // we are using parseInt to check if the date is valid since when the date is unvalid, the datepicker returns "undefined-undefined-"
    if (
      parsedUrlParams.checkinDate &&
      parsedUrlParams.checkinDate !== 'undefined-undefined-' &&
      parsedUrlParams.checkoutDate &&
      parsedUrlParams.checkoutDate !== 'undefined-undefined-'
    ) {
      const checkInDate = parsedUrlParams.checkinDate;
      const checkOutDate = parsedUrlParams.checkoutDate;

      if (isValidDateFormat(checkInDate) && isValidDateFormat(checkOutDate)) {
        this.checkInDate = checkInDate;
        this.checkOutDate = checkOutDate;
      }
    }

    // update the city dropdown

    this.noOfPeople = parsedUrlParams.nbAdults.toString();
    if (this.noOfPeople) {
      this.noOfpeopleElement.textContent = this.noOfPeople;
    }

    this.updateSearchData();
  }

  public updateSearchData() {
    this.cityDropdownInstance.updateCitiesDropdown(this.cityName, this.cityId);

    // This is to ensure that the datepicker is initialized before setting the date
    setTimeout(() => {
      if (this.checkInDate && this.checkOutDate) {
        window.picker.setDateRange(
          new Date(reverseDateFormat(this.checkInDate)),
          new Date(reverseDateFormat(this.checkOutDate))
        );
      }
    }, 1000);

    this.noOfpeopleElement.textContent = this.noOfPeople;

    const SearchParameters: SearchParameters = {
      checkin_date: convertDateFormat(this.checkInDate),
      checkout_date: convertDateFormat(this.checkOutDate),
      no_of_people: this.noOfPeople,
      city_name: this.cityName,
    };
    this.queryUrl = constructUrl(SearchParameters);
  }

  public handleSearch(e: Event) {
    e.preventDefault();
    e.stopPropagation();

    this.cityName = this.cityDropdownInstance.getCityName();
    // this.cityId = this.cityDropdownInstance.getCityId();

    this.checkInDate = this.checkInDateElement.textContent as string;
    this.checkOutDate = this.checkOutDateElement.textContent as string;

    this.noOfPeople = this.noOfpeopleElement.textContent as string;

    const SearchParameters: SearchParameters = {
      checkin_date: convertDateFormat(this.checkInDate),
      checkout_date: convertDateFormat(this.checkOutDate),
      no_of_people: this.noOfPeople,
      nb_children: this.infantCount,
      nb_babies: this.babyCount,
      city_id: this.cityId,
      city_name: this.cityName,
    };

    const constructedUrl = constructUrl(SearchParameters);

    const apartmentsPageUrl = getApartmentsPageUrl();

    const url = apartmentsPageUrl + constructedUrl;
    window.location.href = url;
  }
}
