import { SELECTORS } from './constants';

export class CitiesDropdown {
  public citiesDropdown: HTMLSelectElement;

  constructor() {
    this.citiesDropdown = document.querySelector(SELECTORS.citiesDropdown) as HTMLSelectElement;
  }

  public updateCitiesDropdown(cityName: string, cityId: string) {
    this.citiesDropdown.value = cityName;
    // this.citiesDropdown.setAttribute('city-id', cityId);
  }

  public getCityId(): string {
    return ''; //this.citiesDropdownValue.getAttribute('city-id') as string;
  }

  public getCityName(): string {
    return this.citiesDropdown.value;
  }
}
